import React from "react"
import CaseStudyLayout from '../layouts/case-study'

import SuzanSpinner from '../components/suzanSpinner'

class OohIconSystem extends React.PureComponent {

    render() {
        return (
            <CaseStudyLayout
                slug='susan-spinner'
                heading='Suzan Spinner'
                tags={['Development', 'Expirementation']}
            >
                <SuzanSpinner />
            </CaseStudyLayout>
        )
    }
}

export default OohIconSystem