import React, { useState, useRef, useEffect } from "react"
import times from 'lodash/times'
import anime from 'animejs/lib/anime.es.js'

export default function Layout({children, location}) {
    const itemRefs = []
    const timelines = {
        discRotation: undefined,
        standRotation: undefined
    }
    const disc = React.createRef();
    const config = {
        numItems: 1,
        maxItems: 40,
        discRotationSpeed: 10000
    }

    times(config.maxItems, (i) => {
        itemRefs.push(React.createRef())
    })

    useEffect(
        () => {
            makeTimelines()
            setInitial()
        },
        []
    )

    function getCounterRotationOffset( i ) {
        return ( 360 / config.numItems ) * ( i + 1 )
    }

    function setInitial(method) {
        anime.remove(document.querySelectorAll( '.suzan__item' ))
        anime.remove(document.querySelectorAll( '.suzan__item-stand' ))

        anime({
            targets: document.querySelectorAll( '.suzan__item' ),
            easing: 'easeOutExpo',
            scale: function( el, i, l) {
                return ( i === l - 1 && method === 'add' )
                    ? [ 0, 1 ]
                    : [ 1, 1 ]
            },
            rotate: function( el, i, l ) {
                const rotation = getCounterRotationOffset( i )
                return i === l - 1
                    ? [ rotation - 10 , rotation ]
                    : rotation
            },
            duration: 2000
        })

        timelines.standRotation = anime.timeline({
            easing: 'linear',
            targets: document.querySelectorAll( '.suzan__item-stand' ),
            duration: config.discRotationSpeed,
            loop: true,
            autoplay: false
        }).add({
            rotateZ: function( el, i ) {
                const offset = 0
                const from = 360 - getCounterRotationOffset( i - offset ) + 180
                const to =  0 - getCounterRotationOffset( i - offset ) + 180
                return [ from, to ]
            },
            rotateX: function( el, i ) {
                return [ -90, -90 ]
            }
        })

        timelines.standRotation.seek(timelines.discRotation.progress * 100)
        timelines.standRotation.play()
    }

    function makeTimelines() {
        timelines.discRotation = anime.timeline({
            easing: 'linear',
            targets: disc.current,
            duration: config.discRotationSpeed,
            loop: true,
            autoplay: false
        }).add({
            rotate: [ 0, 360 ],
        })

        timelines.discRotation.play()
    }

    function addItem() {
        if (config.numItems >= config.maxItems) {
            return
        }

        config.numItems += 1
        disc.current.insertAdjacentHTML(
            'beforeend',
            `<li class="suzan__item">
                <div class="suzan__item-plane">
                    <div class="suzan__item-stand">
                        ${config.numItems}
                    </div>
                </div>
            </li>`
        )

        setInitial('add')
    }

    function removeItem() {
        if (config.numItems <= 1) {
            return
        }

        config.numItems -= 1
        document
            .querySelector( '.suzan__item:last-child' )
            .remove()

        setInitial('remove')
    }

    return (
        <>
            <button className="button" onClick={addItem}>Add Item</button>
            <button className="button" onClick={removeItem}>Remove Item</button>
            <div className="suzan">
                <div className="suzan__plane">
                    <ul ref={disc} className="suzan__disc">
                        {times(config.numItems, (i) => (
                            <li ref={itemRefs[i]} className="suzan__item" key={i}>
                                <div className="suzan__item-plane">
                                    <div className="suzan__item-stand">
                                        {i + 1}
                                    </div>
                                </div>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        </>
    )
}